@use "../../../styles/variables" as *;

.questionnaire-container {
  grid-column: 4/13;
  grid-template-rows: 1fr 2fr;
  display: grid;
  grid-template-columns: subgrid;
  background-color: $lime-light;

  .questionnaire-header {
    grid-column: 3 / 8;
    padding: 7.6rem 0;

    .step-title {
      border-bottom: 0.1rem solid black;
    }

    .step-subtitle {
      margin: 4rem 0 2rem 0;

      .number {
        padding-left: 1rem;
        display: inline-block;
      }

      + .description {
        margin-bottom: 4rem;
      }
    }

    .step-subtitle,
    .step-title {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;

      &:after {
        content: "";
        background: transparent;
        border: none;
        width: 1.5rem;
        height: 1.5rem;
        background-size: 1.5rem;
        background: url("../../../../public/icons/arrow-down.svg") no-repeat
          center;
        rotate: 0deg;
        cursor: pointer;
        transition: 0.3s all;
      }

      &.open {
        &:after {
          rotate: 180deg;
        }
      }
    }

    .description {
      overflow: hidden;
      transition: 0.3s all;
    }
  }
}
